import React, { CSSProperties } from 'react';
import { mergeProps } from './mergeProps';

export type IRestProps = {
  id?: string;
  className?: string;
  style?: CSSProperties;
  ['data-testid']?: string;
  ['data-role']?: string;
};

export const mergeRestProps = (restProps: IRestProps, className: string, style?: CSSProperties, events?: Partial<React.DOMAttributes<HTMLElement>>): IRestProps => {

  return mergeProps(
    restProps,
    {
      className,
      style,
      ...events
    }
  );
};


export const proxyRestProps = (restProps: IRestProps, to: string) => {
  const res: IRestProps = {};
  if (typeof restProps['data-testid'] === 'string') {
    res['data-testid'] = restProps['data-testid'] + '__' + to;
  }
  return res;
};

export const spreadRestProps = (restProps: IRestProps, key: string) => {
  const res: IRestProps = {};
  if (typeof restProps['data-testid'] === 'string') {
    res['data-testid'] = restProps['data-testid'] + '_' + key;
  }
  return res;
};